<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered" style="max-width:900px !important">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('ApprovalChain') }}: {{ this.ChainName }} </h4>

                        <button type="button" class="close" v-on:click="ClosePopup">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="col-md-12">
                            <div class="form-group">
                                <div class="listing OuterMainDiv">
                                    <div class="row" style="max-height:500px;overflow-y:auto;">
                                        <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Approval"
                                            SubModuleCode="ApprovalRule" IdentityColumn="APPROVALCHAINGROUPMAPID"
                                            :ListData="GroupData" :HeaderData="Headers" :callbackfunction="FetchData"
                                            :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData"
                                            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                                            v-on:PagerButtonClick="pagerMethod">
                                            <template v-slot:slotdata="{ data }">

                                                <template v-if="data.column.COLUMN_NAME == 'GROUPNAME'">
                                                    {{ data.row.GROUPNAME }}
                                                </template>
                                                <template v-if="data.column.COLUMN_NAME == 'USERS'">
                                                    {{ data.row.USERS }}
                                                </template>
                                                <template v-if="data.column.COLUMN_NAME == 'APPROVERORDER'">
                                                    {{ data.row.APPROVERORDER }}
                                                </template>
                                                <template v-if="data.column.COLUMN_NAME == 'Action'">
                                                    <td class="text-center single-action">
                                                        <div class="cstm-drop-btn">
                                                            <span><a v-on:click="DeleteApprovalGroup(data.row)"><em
                                                                        class="text-primary" aria-hidden="true"><i
                                                                            class="fa fa-trash text-danger action-icon"></i></em></a></span>
                                                        </div>
                                                    </td>
                                                </template>
                                                <template
                                                    v-if="data.column.COLUMN_NAME == 'RankUp' && GroupData.length > 1">
                                                    <template v-if="data.row.APPROVERORDER > 1">
                                                        <span><a
                                                                v-on:click="RankUpDown(data.row.APPROVALCHAINGROUPMAPID, 'RankUp')"><em
                                                                    class="text-primary" aria-hidden="true"> <i
                                                                        class="fa fa-arrow-up text-primary action-icon down"></i></em></a></span>
                                                    </template>
                                                </template>

                                                <template
                                                    v-if="data.column.COLUMN_NAME == 'RankDown' && GroupData.length > 1">
                                                    <template v-if="data.row.APPROVERORDER < GroupData.length">
                                                        <span><a
                                                                v-on:click="RankUpDown(data.row.APPROVALCHAINGROUPMAPID, 'RankDown')"><em
                                                                    class="text-primary" aria-hidden="true"> <i
                                                                        class="fa fa-arrow-down text-primary action-icon down"></i></em></a></span>

                                                    </template>
                                                </template>
                                                <!-- <template v-if="data.column.COLUMN_NAME == 'Action'">
                                                   <span>Action</span>
                                                </template>                                                 -->
                                            </template>
                                        </tg-list>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService';
export default {
    // props: {
    //     ApprovalchainId: String

    // },
    props: {
        ApprovalchainId: {
            type: String
        },
        ApprovalchainName: {
            type: String
        }
    },
    data() {
        return {
            ChainName: '',
            leftSearchFields: [],
            TotalRecords: 0,
            listheaderbuttons: [],
            GroupData: [],
            IsShowGroup: false,
            Headers: [
                {
                    "COLUMN_NAME": 'GROUPNAME',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ApprovalGroupName'),
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'USERS',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Users'),
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'RankUp',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RankUp'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'RankDown',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RankDown'),
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'APPROVERORDER',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Order',
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'Action',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Action'),
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true,
                    }
                }
            ],


        }

    },
    created: function () {
        
        var vm = this;
        vm.ChainName = vm.ApprovalchainName;
        var id = vm.ApprovalchainId;
        vm.FetchData();
    },

    methods: {
        async FetchData() {

            var vueObj = this;
            var url = `approvalChainId=${vueObj.ApprovalchainId}`;
            DataService.ApprovalGroupListByApprovalCHainId(url).then(response => {

                if (response.data != null) {
                    if (response.data.length > 0) {

                        vueObj.GroupData = response.data;
                        //vueObj.TotalRecords = response.data[0].TotalRecords
                        //vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        //vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;

                    } else {
                        vueObj.GroupData = [];
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.GroupData = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap()
                }, 100);
            });
            vueObj.isLoading = false;
        },
        ClosePopup() {
            var vm = this;
            vm.$parent.CloseGroupPopup();

        },
        DeleteApprovalGroup(data) {
            
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  "), function () {
                vm.isLoading = true;
                var param = `id=${data.APPROVALCHAINGROUPMAPID}`
                DataService.DeleteApprovalGroup(param).then(response => {
                    if (response.data == '0') {
                        vm.ShowAlert(vm.$t('DeletedSuccessfully'), "success", true, vm.$t("Alert"));
                    } else if (response.data == '-1') {
                        vm.ShowAlert(vm.$t('Unable to delete,approval chain requires at least one group'), "warning", true, vm.$t("Alert"));
                    }
                    vm.isLoading = false;
                    vm.FetchData();
                });
            });
        },
        RankUpDown(GetId, GetName) {
        
            var vm = this;
            vm.isLoading = true;
            var Chainid = vm.ApprovalchainId
            var GetIndex = vm.GroupData.findIndex(x => x.APPROVALCHAINGROUPMAPID === GetId);
            if (GetName == 'RankUp') {
                var GetNextId = vm.GroupData[GetIndex - 1].APPROVALCHAINGROUPMAPID;
            } else {
                var GetNextId = vm.GroupData[GetIndex + 1].APPROVALCHAINGROUPMAPID;
            }
            vm.confirmR(vm.$t('VerifysequenceofApprovalGroup'), true, false, function (result) {
                var url = `approvalChainId=${Chainid}&approvalChainApprovalGroupMapId1=${GetId}&approvalChainApprovalGroupMapId2=${GetNextId}`
                DataService.SwapApproverOrder(url).then(response => {
                    
                    var arraydata = [];
                    arraydata = JSON.parse(response.data);
                    if (arraydata[0].Status == 0) {
                        vm.ShowAlert(vm.$t('ApprovalChainSuccess'), "success", true, vm.$t("Alert"));
                        vm.FetchData();
                    }
                    vm.isLoading = false;
                })
            })
            vm.isLoading = false;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "DeleteRule":
                    this.DeleteRule()
                    break;
                case "AddNewGroup":
                    this.AddNewGroup()
                    break;
            }
        },
        actionButtonClick(callbackfunction, item) { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions(row) { },

    },
    
}


</script>